.SetUpContainer {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0%;
    left: 0%;
    display: grid;
    grid-template-columns: 20% 60% 20%;
    align-items: center;
    background-image: radial-gradient( white 50%, rgb(35, 35, 66));
}

.PlaceYourShips {
    font-size: 20px;
    height: 30px;
    padding: 0px;
    margin: 0px;
}

.Middle {
    justify-self: center;
    text-align: center;
}

.ShipPicOne {
    width: 100%;
    max-width: 200px;
    height: 100px;
    
}
.ShipPicTwo {
    width: 100%;
    max-width: 300px;
    height: 100px;
}
.ShipPicThree {
    width: 100%;
    max-width: 400px;
    height: 100px;
}
.ShipPicFour {
    width: 100%;
    max-width: 500px;
    height: 100px;
}

.GridContainer {
    justify-self: center;
    display: grid;
    grid-template-columns: repeat(10, 50px);
    background-color: transparent;
    background-image: url(../../../Images/OpenOcean2.gif);
    background-size: 100% 100%;
    
}



.Square {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    cursor: pointer;
    
    
}

.SquareDisabled {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-color: lightsalmon;
    cursor: not-allowed;
}
.SquarePlaced {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-color: transparent;
    cursor: not-allowed;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.Square:hover {
    background-color: lightgreen;
}

.SquareDisabled:hover {
    background-color: lightsalmon;
}

.AxisButton {
    width: 100px;
    height: 40px;
    border: none;
    border-radius: 8px;
    color: black;
    background-image: linear-gradient(to right,  white, rgba(35, 35, 66, 0.5));
    font-size: 25px;
    font-weight: bolder;
    cursor: pointer;
    justify-self: center;
    transition: ease 1s;
}

.AxisButton:hover {
    transform: scale(1.2);
}

.SubmitButton {
    width: 100px;
    height: 40px;
    border: none;
    border-radius: 8px;
    color: black;
    background-image: linear-gradient(to right, rgba(35, 35, 66, 0.5), white);
    font-size: 25px;
    font-weight: bolder;
    cursor: pointer;
    justify-self: center;
    transition: ease 1s;
}

.SubmitButton:hover {
    transform: scale(1.2);
}

.SubmitButton:disabled {
    cursor: not-allowed;
}


