.Container {
    position: absolute;
    top: 0%;
    left: 0%;
    height: 100%;
    width: 100%;
    text-align: center;
    background-image: url(../../../Images/Shine.jpg);
    background-size: 100% 100%;
}

.VictoryDiv {
    background-image: radial-gradient(rgb(245, 248, 245) 50%, transparent);
    height: 30vh;
    width: 45vw;
    margin-left: 27.5vw;
    border-radius: 48px;

}

.Container img {
    height: 100%;
    width: 100%;
}

.Title {
    position: absolute;
    bottom: 0%;
    font-size: 25px;
    background-image: radial-gradient(white, transparent);

}

.PlayAgain {
    margin-top: 60px;
    border: none;
    color: blue;
    background-color: rgba(255, 235, 205, 0.384);
    font-size: 30px;
    height: 70px;
    width: 200px;
    border-radius: 8px;
    transition: ease .3s;

}

.PlayAgain:hover {
    
    cursor: pointer;
    transform: scale(1.3);
}

@media screen and (max-width: 450px) {
    .Title {
        position: absolute;
        bottom: 0%;
        font-size: 15px;
        background-image: radial-gradient(white, transparent);
    
    }
    .VictoryDiv {
        background-image: radial-gradient(rgb(245, 248, 245) 50%, transparent);
        height: 30vh;
        width: 70vw;
        margin-left: 15vw;
        border-radius: 48px;
    
    }
}


@media screen and (max-height: 450px) {
    .Title {
        position: absolute;
        bottom: 0%;
        font-size: 15px;
        background-image: radial-gradient(white, transparent);
    
    }
    
    
}