.Container {
    position: absolute;
    top: 0%;
    left: 0%;
    height: 100%;
    width: 100%;
    text-align: center;
    background-image: url(../../../Images/Shark.jpg);
    background-size: auto 150%;

}



.Title {
    font-size: 15vh;
    font-weight: 900;
    color: transparent;
    text-shadow: 5px 5px 5px rgb(128, 44, 44);
    margin: 0px;
    padding: 0px;
}

.PlayAgain {
    border: none;
    color: blue;
    background-color: rgba(255, 235, 205, 0.164);
    font-size: 30px;
    height: 70px;
    width: 200px;
    border-radius: 8px;
    transition: ease .3s;
    margin-top: 50px;
}

.PlayAgain:hover {
    
    cursor: pointer;
    transform: scale(1.3);
}




@media screen and (max-width: 450px) {

    .Container {
        background-repeat: no-repeat;
        background-size: 180% 140%;
    }
    .Title {
        font-size: 15vw;
        font-weight: 900;
        color: transparent;
        text-shadow: 5px 5px 5px rgb(128, 44, 44);
        margin: 0px;
        padding: 0px;
    }
}


@media screen and (max-height: 450px) {

    .Container {
        background-repeat: no-repeat;
        background-size: 200% 150%;
    }
    
}