.BattleContainer {
    position: absolute;
    background: url(../../../Images/clouds.jpg) no-repeat center center fixed;
    background-size: auto auto;
    top: 0%;
    left: 0%;
    min-width: 100%;
    min-height: 100%;
    margin: 0;
    padding: 0;
    overflow: auto;

}

.BattleSplitter {
    width: 100%;
    text-align: center;
    display: grid;
    grid-template-columns: auto 20% auto;
}



.FriendlyWaters {
    text-align: center;
    justify-self: center;
    
}
.WhoseTurn { 
    height: 50px;
    font-size: 20px;
    background-image: radial-gradient(   rgba(255, 255, 255, 0.823) 10%, rgb(0, 217, 255));
    border-left: .25px solid rgba(0, 0, 0, 0.439);
    border-right: .25px solid rgba(0, 0, 0, 0.439);
    min-width: 500px;
} 
.WhoseTurn img {
    height: 100%;
}



.FriendlyGrid {
    display: grid;
    grid-template-columns: repeat(10, 50px);
    background-image: url(../../../Images/OpenOcean2.gif);
    background-size: 100% 100%;
    min-width: 500px;
}

.FriendlyEmptySquare {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
}
.FriendlyShipOne {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-size: 100% 100%;
}
.FriendlyShipTwo {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-size: 100% 100%;
}
.FriendlyShipThree {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-size: 100% 100%;
}
.FriendlyShipFour {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-size: 100% 100%;
}

.FriendlyShipHit {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
    background-color: rgba(255, 0, 0, 0.2);
    animation: hitAnim 2s linear;
    background-image: url(../../../Images/Hit.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;



}
.FriendlyShipMissed {
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.2);
    background-color: rgba(94, 88, 88, 0.8);
    animation: missAnim 2s linear;
    background-image: url(../../../Images/Sun.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;



}







.EnemyWaters {
    text-align: center;
    justify-self: center;
}

.WhoseTurnEnemy { 
    height: 50px;
    font-size: 20px;
    width: 500px;
    background-image: radial-gradient(   rgba(255, 255, 255, 0.823) 10%, rgb(71, 20, 19));
    border-left: .25px solid rgba(0, 0, 0, 0.439);
    border-right: .25px solid rgba(0, 0, 0, 0.439);
} 
.WhoseTurnEnemy img {
    height: 100%;
}

.EnemyDecorations {
    display: grid;
    grid-template-columns: auto auto auto;
    height: 520px;
}
.TurnTitle {
    background-image: radial-gradient(   rgba(255, 255, 255, 0.823) 10%, rgba(52, 89, 237, 0));
    
    height: 100px;
    margin-left: 1px;
}

.TurnTitle img {
    height: 100%;
    width: 100%;
}
.EnemyDecorations img {
    
    width: 100%;
    height: 100%;
    
}

.EnemyGrid {
    display: grid;
    grid-template-columns: repeat(10, 50px);
    background-image: url(../../../Images/OpenOcean1.gif);
    background-size: 100% 100%;
    animation: flow 12s ease  infinite;
    min-width: 501px;
}

.EnemyEmptySquare {
    cursor: crosshair;
    height: 50px;
    width: 50px;
    border: .25px solid rgba(0, 0, 0, 0.439);
}
.EnemyShipOne {
    height: 50px;
    width: 50px;
    background-color: blue;
    border: .25px solid rgba(0, 0, 0, 0.439);

}
.EnemyShipTwo {
    border: .25px solid rgba(0, 0, 0, 0.439);
    height: 50px;
    width: 50px;
    background-color: green;
}
.EnemyShipThree {
    border: .25px solid rgba(0, 0, 0, 0.439);
    height: 50px;
    width: 50px;
    background-color: orange;
}
.EnemyShipFour {
    border: .25px solid rgba(0, 0, 0, 0.439);
    height: 50px;
    width: 50px;
    background-color: purple;
}

.EnemyShipHit {
    border: .25px solid rgba(0, 0, 0, 0.439);
    height: 50px;
    width: 50px;
    background-image: url(../../../Images/Hit.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;

    background-color: rgba(255, 0, 0, 0.2);
    animation: hitAnim 2s linear;
    cursor: not-allowed;
}

.EnemyShipMissed {
    border: .25px solid rgba(0, 0, 0, 0.439);
    height: 50px;
    width: 50px;
    background-color: rgba(106, 106, 106, 0.2);
    background-image: url(../../../Images/Sun.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    animation: missAnim 2s linear;
    cursor: not-allowed;


}

@keyframes hitAnim {
    0% {transform: scale(1);
        background-color: rgba(255, 0, 0, 0);
        background-size: 0% 0%;
        border-radius: 50%;}
    70% {transform: scale(2);
        background-color: rgba(106, 106, 106, 0.145);
        background-size: 0% 0%;}
    100% {transform: scale(1);
        background-color: rgba(255, 0, 0, 0.6);
        background-size: 0% 0%;
        border-radius: 0%;}
    
}
@keyframes missAnim {
    0% {transform: scale(1);
        background-color: rgba(255, 0, 0, 0);
        background-size: 0% 0%;
        
        border-radius: 50%;}
    70% {transform: scale(2);
        background-color: rgba(106, 106, 106, 0.145);
        border-radius: 50%;}
    100% {transform: scale(1);
        background-color: rgba(106, 106, 106, 0.6);
        background-size: 0% 0%;
        
        border-radius: 0%;}
    
}















