
.Container {
    position: absolute;
    top: 0%;
    left: 0%;
    min-height: 100%;
    min-width: 100%;
    background-color: rgba(0, 0, 0, 0.61);
    overflow: auto;
}


.Modal {
    position: fixed;
    top: 25%;
    left: 25%;
    width: 50%;
    height: 50%;
    background-color: rgba(172, 168, 162, 0.767);
    font-size: 5vh;
    border: 3px solid rgb(18, 19, 18);
    text-align: center;
    line-height: 35vh;
    border-radius: 50%;
    opacity: 0;
    animation: fade 2s linear;
}

.Modal p{
    color: white;
}

@keyframes fade {
    0% {opacity: 0.05;}
    2.5% {opacity: 0.1;}
    5% {opacity: 0.15;}
    7.5% {opacity: 0.2;}
    10% {opacity: 0.25;}
    12.5% {opacity: 0.3;}
    15% {opacity: 0.35;}
    17.5% {opacity: 0.4;}
    20% {opacity: 0.45;}
    22.5% {opacity: 0.5;}
    25% {opacity: 0.55;}
    27.5% {opacity: 0.6;}
    30% {opacity: 0.65;}
    32.5% {opacity: 0.7;}
    35% {opacity: 0.75;}
    37.5% {opacity: 0.8;}
    40% {opacity: 1;}
    70% {opacity: 1;}
    72.5% {opacity: 0.6;}
    75% {opacity: 0.55;}
    77.5% {opacity: 0.5;}
    80% {opacity: 0.45;}
    82.5% {opacity: 0.4;}
    85% {opacity: 0.35;}
    87.5% {opacity: 0.3;}
    90% {opacity: 0.25;}
    92.5% {opacity: 0.2;}
    95% {opacity: 0.15;}
    97.5% {opacity: 0.1;}
    100% {opacity: 0.05;}
}




@media screen and (max-width: 450px) {
    .Modal {
    position: fixed;
    top: calc(50vh - 150px);
    left: calc(50vw - 75px);
    width: 150px;
    height: 150px;
    background-color: rgba(172, 168, 162, 0.767);
    font-size: 20px;
    border: 3px solid rgb(18, 19, 18);
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    opacity: 0;
    animation: fade 2s linear;
    padding-top: 30px;
    margin: 0;
    }
}
