.Container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0%;
    left: 0%;
    text-align: center;
    background-color: rgb(3, 39, 187);
    animation: ripple 12s .6s linear infinite;
    display: grid;
    grid-template-columns: auto;
}
.Container img {
    height: 40vh;
    width: 80vw;
    justify-self: center;

}

.Container button {
    border: none;
    color: rgba(3, 39, 187, 1);
    font-size: 30px;
    cursor: pointer;
    background-color: rgba(0, 244, 251, 0.2);
    border-radius: 8px;
    padding: 10px;
    font-weight: bold;
    width: 25vw;
    height: 10vh;
    justify-self: center;
    min-width: 240px;
    min-height: 80px;
    margin: -15% 0px 0px 0px;
}

.Container button:hover {
    transform: scale(1.5);
}
















